<script>
export default {
  data () {
    return {
      title: 'My Portfolio',
      name: {
        first: 'Nathan',
        last: 'Beal'
      },
      contact: {
        phone: '(360) 878-1408',
        email: 'bealnj@gmail.com'
      },
      about: 'Software Developer with 2 years experience specialized in testing and delivering unique software solutions.',
      socials: {
        github: 'nbeal',
        linkedin: 'nathanbeal'
      },
      profile_file: 'profile.jpg'
    }
  },
  mounted () {
    const $ = this.jquery
    this.$nextTick(() => {
      'use strict' // Start of use strict
      // Smooth scrolling using jQuery easing
      $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
        if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
          var target = $(this.hash)
          target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
          if (target.length) {
            $('html, body').animate({
              scrollTop: (target.offset().top)
            }, 1000, 'easeInOutExpo')
            return false
          }
        }
      })
      // Closes responsive menu when a scroll trigger link is clicked
      $('.js-scroll-trigger').click(function () {
        $('.navbar-collapse').collapse('hide')
      })
      // Activate scrollspy to add active class to navbar items on scroll
      $('body').scrollspy({
        target: '#sideNav'
      })
      this.$ua.trackView({routeName: '/'})
    }) // End of use strict
  },
  methods: {
    require_asset: function (file) {
      if (file) {
        return require('./assets/' + file)
      }
    }
  },
  computed: {
    phone_href: function () {
      return 'tel:' + this.contact.phone.replace(/[ ()-]/g, '')
    },
    email_href: function () {
      return 'mailto:' + this.contact.email
    },
    github_href: function () {
      return 'https://github.com/' + this.socials.github
    },
    linkedin_href: function () {
      return 'https://www.linkedin.com/in/' + this.socials.linkedin
    }
  }
}
</script>

